import {
    ROUTING
} from '../constants/Routing'

export function handleLogout() {
    console.log('test123')

    return (dispatch) => {

        VK.Auth.logout((r) => { // eslint-disable-line no-undef
            console.log(r.session)
            dispatch({
                type: ROUTING,
                payload: {
                    method: 'replace',
                    nextUrl: '/'
                }
            })
        });



    }
}


