import React, {Component} from 'react'
import PropTypes from 'prop-types';
import {browserHistory} from 'react-router'
import {connect} from 'react-redux'
import {bindActionCreators} from 'redux'
import Logout from '../../components/User/Logout'
import * as userLogout from '../../actions/UserLogout'

class Post extends Component {
    constructor() {
        super();
        this.handleSubmit = this.handleSubmit.bind(this)
    }

    handleSubmit(e) {
        e.preventDefault()
        browserHistory.push(`/me/`);
    }

    render() {
        let isLogin = false;
        let template;
        const {user} = this.props;
        const {handleLogout} = this.props.userLogout;

        VK.Auth.getLoginStatus(function (response) { // eslint-disable-line no-undef
            isLogin = response.session
        });
        if (!isLogin) {
            browserHistory.push(`/`);
        } else {
            if (user) {
                template =
                    <div className="user-block">
                        <div className="user-block_photo"><img src={user.image} alt={user.name}/></div>
                        <div className="user-block_name">{user.name}</div>
                        <div className="user-block_age">{user.age}</div>
                        <div className="user-block_id">@{user.domain}</div>
                        <Logout name="logout" handleLogout={handleLogout}/>

                    </div>;
            } else {
                template =
                    <div className="user-block">
                        Не удалось получить данные
                    </div>;
            }
        }
        return (
            <div className='container'>
                <div className="header">
                    <div className="container-inner">
                        {user.name}, мы рады видеть Вас
                    </div>
                </div>
                {template}
                <form className='send-post' onSubmit={this.handleSubmit}>
                    Введите текстовое сообщение для публикации
                    <textarea type='text' placeholder='genreName'></textarea>
                    <button type='submit'>Опубликовать</button>
                </form>
            </div>
        )
    }
}

Post.contextTypes = {
    router: PropTypes.object.isRequired
}

function mapStateToProps(state) {
    return {
        user: state.user,
        page: state.page
    }
}

function mapDispatchToProps(dispatch) {
    return {
        userLogout: bindActionCreators(userLogout, dispatch)
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Post)


